.TodoListItem {
  padding: 1rem;
  display: flex;
  align-items: center;
  &:nth-child(even){
    background: #f8f9fa;
  }

  .Checkbox {
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;

    // checked 아닐 때 svg와 글씨 속성
    svg {
      font-size: 1.5rem;
    }
    .text {
      margin-left: 0.5rem;
      flex: 1;
    }

    &.checked {
      // checked 되면 svg와 글씨가 바뀜
      svg {
        color: #22b8cf;
      }
      .text {
        color: #adb5bd;
        text-decoration: line-through;
      }
    }
  }

  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;
  }

  // 인접 형제 선택자
  & + & {
    border-top: 1px solid #dee2e6;
  }
}